import { BpmnModel } from "../BpmnModel";
import { BpmnModelElement } from "../BpmnModelElement";
import { BpmnModelJsonHelper } from "../BpmnModelJsonHelper";
import { ExtensionMethod } from "../ExtensionMethod";
import { PalParticipant } from "../participant";
import { MexElement } from "./MexElement";

export class PalUserTaskAttribute extends MexElement {
    name: string;
    layer: number;
    description: string;
    code: string;
    input: string;
    output: string;

    responsiblePersons: PalParticipant[] = [];
    accountablePersons: PalParticipant[] = [];
    consultedPersons: PalParticipant[] = [];
    informedPersons: PalParticipant[] = [];

    formDataIds: string[] = [];
    itSystemIds: string[] = [];
    riskIds: string[] = [];
    riskManagementIds: string[] = [];
    systemIds: string[] = [];
    kpiIds: string[] = [];

    constructor(model: BpmnModel) {
        super(model);
    }

    ResetBeforeLoadFrom(): void {
        super.ResetBeforeLoadFrom();

        this.name = null;
        this.layer = 0;
        this.description = null;
        this.code = null;
        this.input = null;
        this.output = null;
        this.responsiblePersons = [];
        this.accountablePersons = [];
        this.consultedPersons = [];
        this.informedPersons = [];
        this.formDataIds = [];
        this.itSystemIds = [];
        this.riskIds = [];
        this.riskManagementIds = [];
        this.systemIds = [];
        this.kpiIds = [];
    }


    ConvertToJsonObject(): {} {
        const obj = super.ConvertToJsonObject();

        ExtensionMethod.SetAttributeValue(obj, "name", this.name);
        ExtensionMethod.SetAttributeValue(obj, "layer", this.layer);
        ExtensionMethod.SetAttributeValue(obj, "description", this.description);
        ExtensionMethod.SetAttributeValue(obj, "code", this.code);
        ExtensionMethod.SetAttributeValue(obj, "input", this.input);
        ExtensionMethod.SetAttributeValue(obj, "output", this.output);
        ExtensionMethod.SetAttributeValue(obj, "responsiblePersons", this.responsiblePersons ? this.responsiblePersons.map(it => it.ConvertToJsonObject()) : []);
        ExtensionMethod.SetAttributeValue(obj, "accountablePersons", this.accountablePersons ? this.accountablePersons.map(it => it.ConvertToJsonObject()) : []);
        ExtensionMethod.SetAttributeValue(obj, "consultedPersons", this.consultedPersons ? this.consultedPersons.map(it => it.ConvertToJsonObject()) : []);
        ExtensionMethod.SetAttributeValue(obj, "informedPersons", this.informedPersons ? this.informedPersons.map(it => it.ConvertToJsonObject()) : []);
        ExtensionMethod.SetAttributeValue(obj, "formDataIds", BpmnModelJsonHelper.StringListToJson(this.formDataIds));
        ExtensionMethod.SetAttributeValue(obj, "itSystemIds", BpmnModelJsonHelper.StringListToJson(this.itSystemIds));
        ExtensionMethod.SetAttributeValue(obj, "riskIds", BpmnModelJsonHelper.StringListToJson(this.riskIds));
        ExtensionMethod.SetAttributeValue(obj, "riskManagementIds", BpmnModelJsonHelper.StringListToJson(this.riskManagementIds));
        ExtensionMethod.SetAttributeValue(obj, "systemIds", BpmnModelJsonHelper.StringListToJson(this.systemIds));
        ExtensionMethod.SetAttributeValue(obj, "kpiIds", BpmnModelJsonHelper.StringListToJson(this.kpiIds));

        return obj;
    }

    LoadFromJsonObject(obj: {}): void {
        super.LoadFromJsonObject(obj);

        this.name = ExtensionMethod.GetAttributeValue4String(obj, "name");
        this.layer = ExtensionMethod.GetAttributeValue4Int64(obj, "layer");
        this.description = ExtensionMethod.GetAttributeValue4String(obj, "description");
        this.code = ExtensionMethod.GetAttributeValue4String(obj, "code");
        this.input = ExtensionMethod.GetAttributeValue4String(obj, "input");
        this.output = ExtensionMethod.GetAttributeValue4String(obj, "output");

        const responsiblePersonObjs = ExtensionMethod.GetAttributeValue(obj, "responsiblePersons") as any[];
        this.responsiblePersons = responsiblePersonObjs ? responsiblePersonObjs.map(it => {
            const d = new PalParticipant(this.Model);
            d.LoadFromJsonObject(it);
            return d;
        }) : [];

        const accountablePersonObjs = ExtensionMethod.GetAttributeValue(obj, "accountablePersons") as any[];
        this.accountablePersons = accountablePersonObjs ? accountablePersonObjs.map(it => {
            const d = new PalParticipant(this.Model);
            d.LoadFromJsonObject(it);
            return d;
        }) : [];

        const consultedPersonObjs = ExtensionMethod.GetAttributeValue(obj, "consultedPersons") as any[];
        this.consultedPersons = consultedPersonObjs ? consultedPersonObjs.map(it => {
            const d = new PalParticipant(this.Model);
            d.LoadFromJsonObject(it);
            return d;
        }) : [];

        const informedPersonObjs = ExtensionMethod.GetAttributeValue(obj, "informedPersons") as any[];
        this.informedPersons = informedPersonObjs ? informedPersonObjs.map(it => {
            const d = new PalParticipant(this.Model);
            d.LoadFromJsonObject(it);
            return d;
        }) : [];

        this.formDataIds = BpmnModelJsonHelper.StringListFromJson(ExtensionMethod.GetAttributeValue4String(obj, "formDataIds"));
        this.itSystemIds = BpmnModelJsonHelper.StringListFromJson(ExtensionMethod.GetAttributeValue4String(obj, "itSystemIds"));
        this.riskIds = BpmnModelJsonHelper.StringListFromJson(ExtensionMethod.GetAttributeValue4String(obj, "riskIds"));
        this.riskManagementIds = BpmnModelJsonHelper.StringListFromJson(ExtensionMethod.GetAttributeValue4String(obj, "riskManagementIds"));
        this.systemIds = BpmnModelJsonHelper.StringListFromJson(ExtensionMethod.GetAttributeValue4String(obj, "systemIds"));
        this.kpiIds = BpmnModelJsonHelper.StringListFromJson(ExtensionMethod.GetAttributeValue4String(obj, "kpiIds"));
    }

    clone(): PalUserTaskAttribute {
        const c = new PalUserTaskAttribute(this.Model);

        c.name = this.name;
        c.layer = this.layer;
        c.description = this.description;
        c.code = this.code;
        c.input = this.input;
        c.output = this.output;
        c.responsiblePersons = this.responsiblePersons.map(it => it.clone());
        c.accountablePersons = this.accountablePersons.map(it => it.clone());
        c.consultedPersons = this.consultedPersons.map(it => it.clone());
        c.informedPersons = this.informedPersons.map(it => it.clone());
        c.formDataIds = this.formDataIds ? this.formDataIds.map(it => it) : [];
        c.itSystemIds = this.itSystemIds ? this.itSystemIds.map(it => it) : [];
        c.riskIds = this.riskIds ? this.riskIds.map(it => it) : [];
        c.riskManagementIds = this.riskManagementIds ? this.riskManagementIds.map(it => it) : [];
        c.systemIds = this.systemIds ? this.systemIds.map(it => it) : [];
        c.kpiIds = this.kpiIds ? this.kpiIds.map(it => it) : [];

        return c;
    }
}